import React from 'react'
import './Planets.css';
function Planets() {
    return (
        
        <div className="Planets__content">
            <div className="Planets__container">
                <p>
                    <label className="Planets_label">
                        Required Production : <input className="Planets_input" type="text" name="Id" ></input>
                    </label>
                </p>
                <p>
                    <label className="Planets_label">
                        Required Material : <input className="Planets_input" type="text" name="Name" ></input>
                    </label>
                </p>
                <p>
                    <label className="Planets_label">
                        Required Purchase Cost : <input className="Planets_input" type="text" name="Location" ></input>
                    </label>
                </p>
                <p>
                    <label className="Planets_label">
                        Direct Material/Cost : <input className="Planets_input" type="text" name="Salary"></input>
                    </label>
                </p>
            </div>
        </div>
    )
}

export default Planets
