import React from "react";
import './App.css';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import Header from "./Header"
import Home from "./Home"
import Calculator1 from "./Calculator1";
import Registerproduct from "./Registerproduct"
import Cvpanalysis from "./Cvpanalysis"
function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
        <Route path="/FFAK:FinancialTarget">
            <Calculator1/>
          </Route> 
          <Route path="/registerproduct">
            <Registerproduct/>
          </Route>
          <Route path="/cvpanalysis">
            <Cvpanalysis/>
          </Route>
          <Route path="/">
            <Header/>
            <Home/>
          </Route>
        </Switch>
      </div>
    </Router>

  );
 
} 

export default App;
