import React from 'react'
import './Budgetincome.css';
function Budgetincome() {
    return (
        <div className="Budgetincome__content">
            <div className="Budgetincome__container">
                <p>
                    <label className="Budgetincome_label">
                        Sales : <input className="Budgetincome_input" type="text" name="Id" ></input>
                    </label>
                </p>
                <p>
                    <label className="Budgetincome_label">
                        Profit : <input className="Budgetincome_input" type="text" name="Name" ></input>
                    </label>
                </p>
                <p>
                    <label className="Budgetincome_label">
                        Overhead : <input className="Budgetincome_input" type="text" name="Location" ></input>
                    </label>
                </p>
                <p>
                    <label className="Budgetincome_label">
                        Total Cost : <input className="Budgetincome_input" type="text" name="Salary"></input>
                    </label>
                </p>
                <p>
                    <label className="Budgetincome_label">
                        Misc Expenses: <input className="Budgetincome_input" type="text" name="Salary"></input>
                    </label>
                </p>
            </div>
            
        </div>
    )
}

export default Budgetincome
