import React from "react";
import "./Header.css";
import {Link} from "react-router-dom";
import Medikit from './medikit.png'
import AccountBalanceWalletRoundedIcon from '@material-ui/icons/AccountBalanceWalletRounded';
import PollIcon from '@material-ui/icons/Poll';
import ListAltIcon from '@material-ui/icons/ListAlt';


function Header() {
    

    return <nav className="header">
        {/*Logo*/}
        <Link to="/">
            <img 
                className="header__logo" 
                src={Medikit}
                alt=""
            />
        </Link>

        {/*Search box*/}
        <div className="header__search">
            <h2>Financial First Aid Kit</h2>
        </div>  

        {/*3 links*/}
        <div className="header__nav">
            
            {/*3rd link*/}
            <Link to={"/registerproduct"} className="header__link">
                <button className="header__option">
                    <ListAltIcon/>
                    <span className="header__optionLineOne">Register Product</span>
                    <span className="header__optionLineTwo"></span>
                </button>
            </Link>
           
            {/*1st link*/}
            <Link to={"/FFAK:FinancialTarget"} className="header__link">
                <button className="header__option">
                    <AccountBalanceWalletRoundedIcon/>
                    <span className="header__optionLineOne">FFAK Financial Target </span>
                    <span className="header__optionLineTwo"></span>
                </button>
            </Link>

            {/*2nd link*/}
            <Link to="/cvpanalysis" className="header__link">
                <button className="header__option">
                    <PollIcon/>
                    <span className="header__optionLineOne">CVP Analysis</span>
                    <span className="header__optionLineTwo"></span>
                </button>
            </Link>

        </div>

        
    </nav>; 
}

export default Header
