import React from 'react'
import './Overheadcost.css';
function Overheadcost() {
    return (
        <div className="Overheadcost__content">
            <div className="Overheadcost__container">
                <p>
                    <label className="Overheadcost_label">
                        Overhead Rate/Unit : <input className="Overheadcost_input" type="text" name="Id" ></input>
                    </label>
                </p>
                <p>
                    <label className="Overheadcost_label">
                        Required Overhead Cost : <input className="Overheadcost_input" type="text" name="Name" ></input>
                    </label>
                </p>
    
            </div>
            
        </div>
    )
}

export default Overheadcost
