import React, {useState} from "react";
import "./Calculator1.css";
import {Link} from "react-router-dom";
import Medikit from './medikit.png'


function Calculator1() {
    // State variables
  const [number1, setNumber1] = useState(0);
  const [number2, setNumber2] = useState(0);
  const [number3, setNumber3] = useState(0);
  const [number4, setNumber4] = useState(0);
  const [number5, setNumber5] = useState(0);
  const [number6, setNumber6] = useState(0);
  const [number7, setNumber7] = useState(0);
  const [number8, setNumber8] = useState(0);
  const [total1, setTotal1] = useState(number1 + number2+number3);
  const [total2, setTotal2] = useState(number4 + number5+number6+number7+number8);
   // Function to add numbers and update total in state
  function calculateTotal1() {
    setTotal1(number1 + number2+number3);
  }
  function calculateTotal2() {
    setTotal2(number4 + number5+number6+number7+number8);
  }

    return ( 
      <div className="Calculator1" >
      
        
        <Link to = "/" className="Calculator1__link1">
            <img 
                className="Calculator1__logo" 
                src={Medikit}
                alt=""
            />
            
        </Link>
        <h2 className="Calculator1__phrase3">FFAK : Financial Target</h2>
        <div className="Calculator1__containerRow">
            
             {/*target Profit*/}
          <div className="Calculator1__container">
           <div>
            <h2 className='Calculator1__phrase4'>Target Profit</h2>
           </div>
           <form>
              <div className="Calculator1__row1">
                <h5 className='Calculator1__phrase1'>Target Profit</h5>
                <input 
                Type='text'             
                placeholder="Put your Target Profit"
                />
              </div>

              <div className="Calculator1__row2">
                <h5 className='Calculator1__phrase1'>Contribution Margin/Unit</h5>
                <input
                Type='text'
                placeholder="Put your Contribution Margin/unit"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Selling Price</h5>
                <input
                Type='text'
                placeholder="Put your Selling Price"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Closing Inventory</h5>
                <input
                Type='text'
                placeholder="Put your Close Inventory"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Opening Inventory</h5>
                <input
                Type='text'
                placeholder="Put your Opening Inventory"
                />
              </div>
              
           </form>
          </div> 
            
            {/*Variable Cost*/}
          <div className="Calculator1__container">
           <div>
            <h2 className='Calculator1__phrase4'>Variable Cost</h2>
           </div>
           <form>
              <div className="Calculator1__row1">
                <h5 className='Calculator1__phrase1'>Direct Material</h5>
                <input               
                value={number1}
                onChange={e => setNumber1(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row2">
                <h5 className='Calculator1__phrase1'>Direct Labour</h5>
                <input
                value={number2}
                onChange={e => setNumber2(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Other Direct Expense</h5>
                <input
                value={number3}
                onChange={e => setNumber3(+e.target.value)}
                placeholder="0"
                />
              </div>
              
           </form>

           <button onClick={calculateTotal1} className="Calculator1__addButton">Total</button>

           <h2 className="Calculator1__phrase2">{total1}</h2>
          </div>  


          {/*Fixed cost*/}
         <div className="Variablecost__container">
          <div>
            <h2 className='Calculator1__phrase4'>Fixed Cost</h2>
          </div>
          <form>
              <div className="Calculator1__row1">
                <h5 className='Calculator1__phrase1'>Rental</h5>
                <input
                className="Calculator1__input"               
                value={number4}
                onChange={e => setNumber4(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row2">
                <h5 className='Calculator1__phrase1'>Insurance</h5>
                <input
                className="Calculator1__input"
                value={number5}
                onChange={e => setNumber5(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Salaries</h5>
                <input
                className="Calculator1__input"
                value={number6}
                onChange={e => setNumber6(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Utilities</h5>
                <input
                className="Calculator1__input"
                value={number7}
                onChange={e => setNumber7(+e.target.value)}
                placeholder="0"
                />
              </div>

              <div className="Calculator1__row3">
                <h5 className='Calculator1__phrase1'>Other Fixed Cost</h5>
                <input
                className="Calculator1__input"
                value={number8}
                onChange={e => setNumber8(+e.target.value)}
                placeholder="0"
                />
              </div>
              
          </form>

          <button onClick={calculateTotal2} className="Calculator1__addButton">Total</button>

          <h2 className="Calculator1__phrase2">{total2}</h2>
         </div>
        </div>

        
      </div>

    );
}

export default Calculator1
//onChange={e => setNumber2(+e.target.value)}
//onChange={e => setNumber1(+e.target.value)}