import React, {useState} from 'react';
import './Registerproduct.css';
import {Link} from "react-router-dom";
import Medikit from './medikit.png';
import JsonData from "./MOCK_DATA.json";
import ReactPaginate from "react-paginate";
function Registerproduct() {
    const [users,setUsers]=useState(JsonData.slice(0,4));
    const [pageNumber,setPageNumber] = useState(0)

    const usersPerPage = 1
    const pagesVisited = pageNumber * usersPerPage

    const displayUsers = users
        .slice(pagesVisited, pagesVisited+usersPerPage)
        .map(user =>{
            return(
                <div className="user">
                    <h3>{user.Company_name}</h3>
                    <h3>{user.Product_name}</h3>
                    <h3>{user.Type_of_bussiness}</h3>
                    <h3>{user.Financial_Period}</h3>   
                </div>
            );
        });

    
        const pageCount=Math.ceil(users.length/usersPerPage);
        const changePage=({selected})=> {
            setPageNumber(selected);
        }
    return (
        
        
        <div className="Registerproduct">
            <Link to = "/" className="Calculator1__link1">
                <img 
                    className="Calculator1__logo" 
                    src={Medikit}
                    alt=""
                />
            </Link>
            <h2 className="Calculator1__phrase3">REGISTER PRODUCT</h2>
            {displayUsers}
            <ReactPaginate
                previousLabel={"Previous"}
                nextLabel={"Next"}
                pageCount={pageCount}
                onPageChange={changePage}
                containerClassName={"paginationBttns"}
                previousLinkClassName={"previousBttn"}
                nextLinkClassName={"nextBttn"}
                disabledClassName={"paginationDisabled"}
                activeClassName={"paginationActive"}
            />
        </div>
    );
}

export default Registerproduct
