import React from 'react'
import './Navbar.css'

const Navbar = ({setPage}) => {
    return ( 
        <nav>
            <button className="Navbar_button" onClick={()=>setPage('planets')}>Direct Material Budget</button>
            <button className="Navbar_button" onClick={()=>setPage('people')}>Direct Labour Budget</button>
            <button className="Navbar_button" onClick={()=>setPage('overheadcost')}>Overhead Cost Budget</button>
            <button className="Navbar_button" onClick={()=>setPage('budgetincome')}>Budgeted Income Statement</button>
        </nav>
     );
}
 
export default Navbar;
