import React, {useState} from 'react'
import "./Cvpanalysis.css";
import Planets from "./Planets";
import People from "./People";
import Navbar from "./Navbar";
import Overheadcost from "./Overheadcost";
import Budgetincome from "./Budgetincome";
import {Link} from "react-router-dom";
import Medikit from './medikit.png';

function Cvpanalysis() {
    const[page,setPage]=useState('planets');
    
    return (
        <div className="Cvpanalysis_background">
            <div className="Cvpanalysis_body">
                
                <Link to = "/" className="Cvpanalysis_phrase1">
                <img 
                    className="Calculator1__logo" 
                    src={Medikit}
                    alt=""
                />
                </Link>
                <h2 className="Calculator1__phrase3">CVP ANALYSIS</h2>
                <Navbar setPage={setPage}/>
                <div className="Cvpanalysis_content">
                    {/*{page === 'planets'? <Planets/> : <People/> && <Overheadcost/>&&<Budgetincome/>}*/}
                    {page === 'planets'&& (
                        <div className="checkout__right">
                            <Planets/>
                        </div>
                    )}

                    {page === 'people'&& (
                        <div className="checkout__right">
                            <People/> 
                        </div>
                    )}

                    {page === 'overheadcost' && (
                        <div className="checkout__right">
                            <Overheadcost/> 
                        </div>
                    )}

                    {page === 'budgetincome' && (
                        <div className="checkout__right">
                            <Budgetincome/> 
                        </div>
                    )}
                </div>
            </div>
        </div>
    )
}

export default Cvpanalysis;
