import React from 'react'
import "./Home.css";
import Finance from './Finance.png'
import Profile from './Profile4.png'
import AccountBalanceWalletRoundedIcon from '@material-ui/icons/AccountBalanceWalletRounded';
import PollIcon from '@material-ui/icons/Poll';
import {Link} from "react-router-dom";

function Home() {
    return (
        <div className='Home__main'>
            <div className="Home__welcome">
                <div className="Home__welcomePhrase">
                    <h1>Welcome to Financial First Aid Kit</h1>
                    <h3>In those critical moments, your focus will be on your family's safety. ... The EFFAK helps you organize critical financial, medical, and household information. It includes a checklist of important documents and forms to compile your relevant information.</h3>
                </div>
                <img 
                    className="Home__welcomeLogo" 
                    src={Finance}
                    alt=""
                />
            </div>
            <div className="Home__features">
                <div className="Home__featuresPhrase">
                    <h1>Features</h1>
                </div>
                <div className="Home__featuresButton">
                    <div className="Home__nav">
                        
                        <Link to="/registerproduct" className="Home__link">
                            <div className="Home__option">
                                <PollIcon/>
                                <span className="Home__optionLineOne">Register Product</span>
                                <span className="Home__optionLineTwo"></span>
                            </div>
                        </Link>
                        
                        {/*1st link*/}
                        <Link to={"/FFAK:FinancialTarget"} className="Home__link">
                            <div className="Home__option">
                                <AccountBalanceWalletRoundedIcon/>
                                <span className="Home__optionLineOne">FFAK Financial Target </span>
                                <span className="Home__optionLineTwo"></span>
                            </div>
                        </Link>

                        {/*2nd link*/}
                        <Link to="/cvpanalysis" className="Home__link">
                            <div className="Home__option">
                                <PollIcon/>
                                <span className="Home__optionLineOne">CVP Analysis</span>
                                <span className="Home__optionLineTwo"></span>
                            </div>
                        </Link>

                    </div>
                </div>
                
            </div>
            <div className="Home__Profile">
                <div className="Home__profilePhrase">
                    <h1>Profiles</h1>
                </div>
                <div className="Home__profileContainer">
                    <div className="Home__profileNav">
                            {/*1st Container profile*/}                     
                            <img 
                                className="Home__ProfileLogo" 
                                src={Profile}
                                alt=""
                             />
                            <span className="Home__profileoptionLineOne">Name: DR IR BadrulHisyam </span>
                            <span className="Home__profileoptionLineTwo">Job: Developer</span>
                                              
                    </div>
                </div>
                
            </div>
            
        </div>
    )
}

export default Home
