import React from 'react'
import './People.css';

function People() {
    return (
        <div className="People__content">
            <div className="People__container">
                <p>
                    <label className="People_label">
                        LH/Unit : <input className="People_input" type="text" name="Id" ></input>
                    </label>
                </p>
                <p>
                    <label className="People_label">
                        Required Labours Hours : <input className="People_input" type="text" name="Name" ></input>
                    </label>
                </p>
                <p>
                    <label className="People_label">
                        Required Labour Cost : <input className="People_input" type="text" name="Location" ></input>
                    </label>
                </p>

            </div>
            
        </div>
    )
}

export default People
